.App {
  background-color: white;
  min-height: 100vh;
}

.wrapper{
  width: 100%;
  max-width: 1100px;
  margin: 0 auto;

  @media (max-width:800px) {
    width: 90%;
  }
}

.contact-form{
  width: 50%;
}


#name {
  width: 100%;
  font-size: 1rem;
  padding: 13px 10px;
  background: #F5F7FA;
  border: 2px solid #000;
  border-radius: 6px;
  margin-bottom: 25px;
}

#email{
  width: 100%;
  font-size: 1rem;
  padding: 13px 10px;
  background: #F5F7FA;
  border: 2px solid #000;
  border-radius: 6px;
  margin-bottom: 25px;
}

.button-form, #submit_app {
  background: #fff;
  border: 2px solid;
  border-radius: 0.5rem;
  border-width: 2px;
  color: inherit;
  display: inline-block;
  text-decoration: none;
  transition: background .2s ease,border-color .2s ease,color .2s ease;
  font-size: .9rem;
  padding: 16px;
  font-weight: 600;
}

 textarea {
  width: 100%;
  font-size: 1rem;
  padding: 13px 10px;
  background: #F5F7FA;
  border: 2px solid #000;
  border-radius: 6px;
  margin-bottom: 25px;
}

